<template>
  <div :class="classes">
    <button
      type="button"
      :class="arrowClasses"
      class="left"
      @click="arrowEvent(-1)"
    >
      <icon type="chevron-left">
      </icon>
    </button>
    <div :class="[prefixCls + '-list']">
      <div
        ref="originTrack"
        :class="[prefixCls + '-track', showCopyTrack ? '' : 'higher']"
        :style="trackStyles"
      >
        <slot></slot>
      </div>
      <div
        v-if="loop"
        ref="copyTrack"
        :class="[prefixCls + '-track', showCopyTrack ? 'higher' : '']"
        :style="copyTrackStyles"
      >
      </div>
    </div>
    <button
      type="button"
      :class="arrowClasses"
      class="right"
      @click="arrowEvent(1)"
    >
      <icon type="chevron-right">
      </icon>
    </button>
    <ul :class="dotsClasses">
      <template v-for="n in slides.length">
        <li
          :key="n"
          :class="[n - 1 === currentIndex ? prefixCls + '-active' : '']"
          @click="dotsEvent('click', n - 1)"
          @mouseover="dotsEvent('hover', n - 1)"
        >
          <button
            type="button"
            :class="[radiusDot ? 'radius' : '']"
          >
          </button>
        </li>
      </template>
    </ul>
  </div>
</template>

<script>
import isOneOf from 'Global/Assets/isOneOf';
import Icon from '../icon/icon.vue';

const prefixCls = 'ivu-carousel';

export default {
  name: 'Carousel',

  components: {Icon},

  props: {
    arrow: {
      default: 'hover',
      type: String,
      validator(value) {
        return isOneOf(value, ['hover', 'always', 'never']);
      },
    },
    autoplay: {
      default: false,
      type: Boolean,
    },
    autoplaySpeed: {
      default: 2000,
      type: Number,
    },
    dots: {
      default: 'inside',
      type: String,
      validator(value) {
        return isOneOf(value, ['inside', 'outside', 'none']);
      },
    },
    easing: {
      default: 'ease',
      type: String,
    },
    height: {
      default: 'auto',
      type: [String, Number],
      validator(value) {
        return value === 'auto' || Object.prototype.toString.call(value) === '[object Number]';
      },
    },
    loop: {
      default: false,
      type: Boolean,
    },
    radiusDot: {
      default: false,
      type: Boolean,
    },
    trigger: {
      default: 'click',
      type: String,
      validator(value) {
        return isOneOf(value, ['click', 'hover']);
      },
    },
    value: {
      default: 0,
      type: Number,
    },
  },

  data() {
    return {
      copyTrackIndex: this.value,
      currentIndex: this.value,
      // 默认左滑
      hideTrackPos: -1,
      listWidth: 0,
      prefixCls,
      ready: false,
      showCopyTrack: false,
      slideInstances: [],
      slides: [],
      timer: null,
      trackCopyOffset: 0,
      trackIndex: this.value,
      trackOffset: 0,
      trackWidth: 0,
    };
  },

  computed: {
    arrowClasses() {
      return [`${prefixCls}-arrow`, `${prefixCls}-arrow-${this.arrow}`];
    },
    classes() {
      return [`${prefixCls}`];
    },
    copyTrackStyles() {
      return {
        position: 'absolute',
        top: 0,
        transform: `translate3d(${-this.trackCopyOffset}px, 0px, 0px)`,
        transition: `transform 500ms ${this.easing}`,
        width: `${this.trackWidth}px`,
      };
    },
    dotsClasses() {
      return [`${prefixCls}-dots`, `${prefixCls}-dots-${this.dots}`];
    },
    trackStyles() {
      return {
        transform: `translate3d(${-this.trackOffset}px, 0px, 0px)`,
        transition: `transform 500ms ${this.easing}`,
        width: `${this.trackWidth}px`,
      };
    },
  },

  watch: {
    autoplay() {
      this.setAutoplay();
    },
    autoplaySpeed() {
      this.setAutoplay();
    },
    copyTrackIndex() {
      this.updateOffset();
    },
    height() {
      this.updatePos();
    },
    trackIndex() {
      this.updateOffset();
    },
    value(val) {
      this.currentIndex = val;
      this.trackIndex = val;
    },
  },

  mounted() {
    this.updateSlides(true);
    this.handleResize();
    this.setAutoplay();
    window.addEventListener('resize', this.handleResize, false);
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize, false);
  },

  methods: {
    add(offset) {
      // 获取单个轨道的图片数
      const slidesLen = this.slides.length;

      // 如果是无缝滚动，需要初始化双轨道位置
      if (this.loop) {
        if (offset > 0) {
          // 初始化左滑轨道位置
          this.hideTrackPos = -1;
        } else {
          // 初始化右滑轨道位置
          this.hideTrackPos = slidesLen;
        }

        this.updateTrackPos(this.hideTrackPos);
      }

      // 获取当前展示图片的索引值
      const oldIndex = this.showCopyTrack ? this.copyTrackIndex : this.trackIndex;
      let index = oldIndex + offset;
      while (index < 0) {
        index += slidesLen;
      }

      if (((offset > 0 && index === slidesLen) || (offset < 0 && index === slidesLen - 1)) && this.loop) {
        // 极限值（左滑：当前索引为总图片张数， 右滑：当前索引为总图片张数 - 1）切换轨道
        this.showCopyTrack = !this.showCopyTrack;
        this.trackIndex += offset;
        this.copyTrackIndex += offset;
      } else {
        if (!this.loop) {
          index %= this.slides.length;
        }

        this.updateTrackIndex(index);
      }

      this.currentIndex = index === this.slides.length ? 0 : index;
      this.$emit('on-change', oldIndex, this.currentIndex);
      this.$emit('input', this.currentIndex);
    },
    arrowEvent(offset) {
      this.setAutoplay();
      this.add(offset);
    },
    dotsEvent(event, n) {
      const curIndex = this.showCopyTrack ? this.copyTrackIndex : this.trackIndex;

      if (event === this.trigger && curIndex !== n) {
        this.updateTrackIndex(n);
        this.$emit('input', n);
        // Reset autoplay timer when trigger be activated
        this.setAutoplay();
      }
    },
    // find option component
    findChild(cb) {
      const find = function _find(child) {
        const name = child.$options.componentName;

        if (name) {
          cb(child);
        } else if (child.$children.length) {
          child.$children.forEach((innerChild) => {
            find(innerChild, cb);
          });
        }
      };

      if (this.slideInstances.length || !this.$children) {
        this.slideInstances.forEach((child) => {
          find(child);
        });
      } else {
        this.$children.forEach((child) => {
          find(child);
        });
      }
    },
    handleResize() {
      this.listWidth = parseInt(window.getComputedStyle(this.$el, 'width'), 10);
      this.updatePos();
      this.updateOffset();
    },
    // copy trackDom
    initCopyTrackDom() {
      this.$nextTick(() => {
        this.$refs.copyTrack.innerHTML = this.$refs.originTrack.innerHTML;
      });
    },
    setAutoplay() {
      window.clearInterval(this.timer);

      if (this.autoplay) {
        this.timer = window.setInterval(() => {
          this.add(1);
        }, this.autoplaySpeed);
      }
    },
    // use when slot changed
    slotChange() {
      this.$nextTick(() => {
        this.slides = [];
        this.slideInstances = [];

        this.updateSlides(true, true);
        this.updatePos();
        this.updateOffset();
      });
    },
    updateOffset() {
      this.$nextTick(() => {
        /* hack: revise copyTrack offset (1px) */
        const ofs = this.copyTrackIndex > 0 ? -1 : 1;
        this.trackOffset = this.trackIndex * this.listWidth;
        this.trackCopyOffset = this.copyTrackIndex * this.listWidth + ofs;
      });
    },
    updatePos() {
      this.findChild((child) => {
        child.width = this.listWidth;
        child.height = typeof this.height === 'number' ? `${this.height}px` : this.height;
      });

      this.trackWidth = (this.slides.length || 0) * this.listWidth;
    },
    updateSlides(init) {
      const slides = [];
      let index = 1;

      this.findChild((child) => {
        slides.push({
          $el: child.$el,
        });
        child.index = index;
        index += 1;

        if (init) {
          this.slideInstances.push(child);
        }
      });

      this.slides = slides;
      this.updatePos();
    },
    updateTrackIndex(index) {
      if (this.showCopyTrack) {
        this.copyTrackIndex = index;
      } else {
        this.trackIndex = index;
      }
    },
    updateTrackPos(index) {
      if (this.showCopyTrack) {
        this.trackIndex = index;
      } else {
        this.copyTrackIndex = index;
      }
    },
  },
};
</script>
