<template>
  <div :class="classes">
    <div
      ref="hours"
      :class="[prefixCls+ '-list']"
    >
      <ul :class="[prefixCls + '-ul']">
        <li
          v-for="(item, i) in hoursList"
          v-show="!item.hide"
          :key="i"
          :class="getCellCls(item)"
          @click="handleClick('hours', item)"
        >
          {{ formatTime(item.text) }}
        </li>
      </ul>
    </div>
    <div
      ref="minutes"
      :class="[prefixCls+ '-list']"
    >
      <ul :class="[prefixCls + '-ul']">
        <li
          v-for="(item, i) in minutesList"
          v-show="!item.hide"
          :key="i"
          :class="getCellCls(item)"
          @click="handleClick('minutes', item)"
        >
          {{ formatTime(item.text) }}
        </li>
      </ul>
    </div>
    <div
      v-show="showSeconds"
      ref="seconds"
      :class="[prefixCls+ '-list']"
    >
      <ul :class="[prefixCls + '-ul']">
        <li
          v-for="(item, i) in secondsList"
          v-show="!item.hide"
          :key="i"
          :class="getCellCls(item)"
          @click="handleClick('seconds', item)"
        >
          {{ formatTime(item.text) }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import stubArray from 'lodash/stubArray';
import cloneDeep from 'lodash/cloneDeep';
import upperFirst from 'lodash/upperFirst';
import {scrollTop} from 'Src/utils/assist';
import Options from '../time-mixins';

const prefixCls = 'ivu-time-picker-cells';
const timeParts = ['hours', 'minutes', 'seconds'];

export default {
  name: 'TimeSpinner',

  mixins: [Options],

  props: {
    hours: {
      default: NaN,
      type: [Number, String],
    },
    minutes: {
      default: NaN,
      type: [Number, String],
    },
    seconds: {
      default: NaN,
      type: [Number, String],
    },
    showSeconds: {
      default: true,
      type: Boolean,
    },
    steps: {
      default: stubArray,
      type: Array,
    },
  },

  data() {
    return {
      compiled: false,
      // which column inside the picker
      focusedColumn: -1,
      // the values array into [hh, mm, ss]
      focusedTime: [0, 0, 0],
      prefixCls,
      spinerSteps: [1, 1, 1].map((one, i) => Math.abs(this.steps[i]) || one),
    };
  },

  computed: {
    classes() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-with-seconds`]: this.showSeconds,
        },
      ];
    },
    hoursList() {
      const hours = [];
      const step = this.spinerSteps[0];
      const focusedHour = this.focusedColumn === 0 && this.focusedTime[0];
      const hourTmpl = {
        disabled: false,
        hide: false,
        selected: false,
        text: 0,
      };

      for (let i = 0; i < 24; i += step) {
        const hour = cloneDeep(hourTmpl);
        hour.text = i;
        hour.focused = i === focusedHour;

        if (this.disabledHours.length && this.disabledHours.indexOf(i) > -1) {
          hour.disabled = true;

          if (this.hideDisabledOptions) {
            hour.hide = true;
          }
        }

        if (this.hours === i) {
          hour.selected = true;
        }

        hours.push(hour);
      }

      return hours;
    },
    minutesList() {
      const minutes = [];
      const step = this.spinerSteps[1];
      const focusedMinute = this.focusedColumn === 1 && this.focusedTime[1];
      const minuteTmpl = {
        disabled: false,
        hide: false,
        selected: false,
        text: 0,
      };

      for (let i = 0; i < 60; i += step) {
        const minute = cloneDeep(minuteTmpl);
        minute.text = i;
        minute.focused = i === focusedMinute;

        if (this.disabledMinutes.length && this.disabledMinutes.indexOf(i) > -1) {
          minute.disabled = true;

          if (this.hideDisabledOptions) {
            minute.hide = true;
          }
        }

        if (this.minutes === i) {
          minute.selected = true;
        }

        minutes.push(minute);
      }

      return minutes;
    },
    secondsList() {
      const seconds = [];
      const step = this.spinerSteps[2];
      const focusedMinute = this.focusedColumn === 2 && this.focusedTime[2];
      const secondTmpl = {
        disabled: false,
        hide: false,
        selected: false,
        text: 0,
      };

      for (let i = 0; i < 60; i += step) {
        const second = cloneDeep(secondTmpl);
        second.text = i;
        second.focused = i === focusedMinute;

        if (this.disabledSeconds.length && this.disabledSeconds.indexOf(i) > -1) {
          second.disabled = true;

          if (this.hideDisabledOptions) {
            second.hide = true;
          }
        }

        if (this.seconds === i) {
          second.selected = true;
        }

        seconds.push(second);
      }

      return seconds;
    },
  },

  watch: {
    focusedTime(updated, old) {
      timeParts.forEach((part, i) => {
        if (updated[i] === old[i] || typeof updated[i] === 'undefined') {
          return;
        }

        const valueIndex = this[`${part}List`].findIndex((obj) => obj.text === updated[i]);
        this.scroll(part, valueIndex);
      });
    },
    hours(val) {
      if (!this.compiled) {
        return;
      }

      this.scroll('hours', this.hoursList.findIndex((obj) => obj.text === val));
    },
    minutes(val) {
      if (!this.compiled) {
        return;
      }

      this.scroll('minutes', this.minutesList.findIndex((obj) => obj.text === val));
    },
    seconds(val) {
      if (!this.compiled) {
        return;
      }

      this.scroll('seconds', this.secondsList.findIndex((obj) => obj.text === val));
    },
  },

  mounted() {
    this.$nextTick(() => {
      this.compiled = true;
    });
  },

  methods: {
    chooseValue(values) {
      const changes = timeParts.reduce((obj, part, i) => {
        const value = values[i];

        if (this[part] === value) {
          return obj;
        }

        return {
          ...obj,
          [part]: value,
        };
      }, {});

      if (Object.keys(changes).length > 0) {
        this.emitChange(changes);
      }
    },
    emitChange(changes) {
      this.$emit('on-change', changes);
      this.$emit('on-pick-click');
    },
    formatTime(text) {
      return text < 10 ? `0${text}` : text;
    },
    getCellCls(cell) {
      return [
        `${prefixCls}-cell`,
        {
          [`${prefixCls}-cell-selected`]: cell.selected,
          [`${prefixCls}-cell-focused`]: cell.focused,
          [`${prefixCls}-cell-disabled`]: cell.disabled,
        },
      ];
    },
    getScrollIndex(type, idx) {
      let index = idx;
      const Type = upperFirst(type);
      const disabled = this[`disabled${Type}`];

      if (disabled.length && this.hideDisabledOptions) {
        let count = 0;
        disabled.forEach((item) => {
          if (item <= index) {
            count += 1;
          }
        });

        index -= count;
      }

      return index;
    },
    handleClick(type, cell) {
      if (cell.disabled) {
        return;
      }

      const data = {[type]: cell.text};
      this.emitChange(data);
    },
    scroll(type, index) {
      const from = this.$refs[type].scrollTop;
      const to = 24 * this.getScrollIndex(type, index);
      scrollTop(this.$refs[type], from, to, 500);
    },
    updateFocusedTime(col, time) {
      this.focusedColumn = col;
      this.focusedTime = time.slice();
    },
    updateScroll() {
      this.$nextTick(() => {
        timeParts.forEach((type) => {
          this.$refs[type].scrollTop = 24 * this[`${type}List`].findIndex((obj) => obj.text === this[type]);
        });
      });
    },
  },
};
</script>
