<template>
  <table
    cellspacing="0"
    cellpadding="0"
    border="0"
    :style="styleObject"
  >
    <colgroup>
      <col
        v-for="(column, i) in columns"
        :key="i"
        :width="setCellWidth(column)"
      >
    </colgroup>
    <tbody :class="[prefixCls + '-tbody']">
      <template v-for="(row, index) in data">
        <table-tr
          :key="row._rowKey"
          :row="row"
          :prefix-cls="prefixCls"
          @mouseenter.native.stop="handleMouseIn(row._index)"
          @mouseleave.native.stop="handleMouseOut(row._index)"
          @click.native="clickCurrentRow(row._index)"
          @dblclick.native.stop="dblclickCurrentRow(row._index)"
        >
          <td
            v-for="(column, i) in columns"
            :key="i"
            :class="alignCls(column, row)"
          >
            <cell
              :key="column._columnKey"
              :fixed="fixed"
              :prefix-cls="prefixCls"
              :row="row"
              :column="column"
              :natural-index="index"
              :index="row._index"
              :checked="rowChecked(row._index)"
              :disabled="rowDisabled(row._index)"
              :expanded="rowExpanded(row._index)"
            >
            </cell>
          </td>
        </table-tr>
        <tr
          v-if="rowExpanded(row._index)"
          :key="row._index"
          :class="{[prefixCls + '-expanded-hidden']: fixed}"
        >
          <td
            :colspan="columns.length"
            :class="prefixCls + '-expanded-cell'"
          >
            <expand
              :key="row._rowKey"
              :row="row"
              :render="expandRender"
              :index="row._index"
            >
            </expand>
          </td>
        </tr>
      </template>
    </tbody>
  </table>
</template>

<script>
import get from 'lodash/get';
// todo :key="row"
import TableTr from './table-tr.vue';
import Cell from './cell.vue';
import Expand from './expand';
import Mixin from './mixin';

export default {
  name: 'TableBody',

  components: {Cell, Expand, TableTr},

  mixins: [Mixin],

  props: {
    columns: {
      default: undefined,
      type: Array,
    },
    columnsWidth: {
      default: undefined,
      type: Object,
    },
    // rebuildData
    data: {
      default: undefined,
      type: Array,
    },
    fixed: {
      default: false,
      type: [Boolean, String],
    },
    objData: {
      default: undefined,
      type: Object,
    },
    prefixCls: {
      default: undefined,
      type: String,
    },
    styleObject: {
      default: undefined,
      type: Object,
    },
  },

  computed: {
    expandRender() {
      let renderFn = function render() {
        return '';
      };

      for (let i = 0; i < this.columns.length; i += 1) {
        const column = this.columns[i];

        if (column.type && column.type === 'expand') {
          if (column.render) {
            renderFn = column.render;
          }
        }
      }

      return renderFn;
    },
  },
  methods: {
    clickCurrentRow(_index) {
      this.$parent.clickCurrentRow(_index);
    },
    dblclickCurrentRow(_index) {
      this.$parent.dblclickCurrentRow(_index);
    },
    handleMouseIn(_index) {
      this.$parent.handleMouseIn(_index);
    },
    handleMouseOut(_index) {
      this.$parent.handleMouseOut(_index);
    },
    rowChecked(_index) {
      return get(this.objData, `[${_index}]._isChecked`);
    },
    rowDisabled(_index) {
      return get(this.objData, `[${_index}]._isDisabled`);
    },
    rowExpanded(_index) {
      return get(this.objData, `[${_index}]._isExpanded`);
    },
  },
};
</script>
