import dateUtil from '../../utils/date';

export const toDate = function _toDate(date) {
  let dateClone = new Date(date);

  // IE patch start (#1422)
  if (Number.isNaN(dateClone.getTime()) && typeof date === 'string') {
    dateClone = date.split('-').map(Number);
    dateClone[1] += 1;
    dateClone = new Date(...dateClone);
  }
  // IE patch end

  if (Number.isNaN(dateClone.getTime())) {
    return null;
  }

  return dateClone;
};

export const clearHours = function _clearHours(time) {
  const cloneDate = new Date(time);
  cloneDate.setHours(0, 0, 0, 0);

  return cloneDate.getTime();
};

export const isInRange = (time, a, b) => {
  if (!a || !b) {
    return false;
  }

  const [start, end] = [a, b].sort();

  return time >= start && time <= end;
};

export const formatDate = function _formatDate(date, format) {
  const realDate = toDate(date);

  if (!realDate) {
    return '';
  }

  return dateUtil.format(realDate, format || 'yyyy-MM-dd');
};

export const parseDate = function _parseDate(string, format) {
  return dateUtil.parse(string, format || 'yyyy-MM-dd');
};

export const getDayCountOfMonth = function _getDayCountOfMonth(year, month) {
  return new Date(year, month + 1, 0).getDate();
};

export const getFirstDayOfMonth = function _getFirstDayOfMonth(date) {
  const temp = new Date(date.getTime());
  temp.setDate(1);

  return temp.getDay();
};

export const siblingMonth = function _siblingMonth(src, diff) {
  const temp = new Date(src); // lets copy it so we don't change the original
  const newMonth = temp.getMonth() + diff;
  const newMonthDayCount = getDayCountOfMonth(temp.getFullYear(), newMonth);

  if (newMonthDayCount < temp.getDate()) {
    temp.setDate(newMonthDayCount);
  }

  temp.setMonth(newMonth);

  return temp;
};

export const prevMonth = function _prevMonth(src) {
  return siblingMonth(src, -1);
};

export const nextMonth = function _nextMonth(src) {
  return siblingMonth(src, 1);
};

export const initTimeDate = function _initTimeDate() {
  const date = new Date();
  date.setHours(0);
  date.setMinutes(0);
  date.setSeconds(0);

  return date;
};

export const formatDateLabels = (function iife() {
  /*
      Formats:
      yyyy - 4 digit year
      m - month, numeric, 1 - 12
      mm - month, numeric, 01 - 12
      mmm - month, 3 letters, as in `toLocaleDateString`
      Mmm - month, 3 letters, capitalize the return from `toLocaleDateString`
      mmmm - month, full name, as in `toLocaleDateString`
      Mmmm - month, full name, capitalize the return from `toLocaleDateString`
    */

  const formats = {
    m: (date) => date.getMonth() + 1,
    mm: (date) => `0${date.getMonth() + 1}`.slice(-2),
    mmm: (date, locale) => {
      const monthName = date.toLocaleDateString(locale, {
        month: 'long',
      });

      return monthName.slice(0, 3);
    },
    Mmm: (date, locale) => {
      const monthName = date.toLocaleDateString(locale, {
        month: 'long',
      });

      return (monthName[0].toUpperCase() + monthName.slice(1).toLowerCase()).slice(0, 3);
    },
    mmmm: (date, locale) =>
      date.toLocaleDateString(locale, {
        month: 'long',
      }),
    Mmmm: (date, locale) => {
      const monthName = date.toLocaleDateString(locale, {
        month: 'long',
      });

      return monthName[0].toUpperCase() + monthName.slice(1).toLowerCase();
    },
    yyyy: (date) => date.getFullYear(),
  };
  const formatRegex = new RegExp(['yyyy', 'Mmmm', 'mmmm', 'Mmm', 'mmm', 'mm', 'm'].join('|'), 'g');

  return function _formatDateLabels(locale, format, date) {
    const componetsRegex = /(\[[^\]]+])([^[\]]+)(\[[^\]]+])/;
    const components = format.match(componetsRegex).slice(1);
    const separator = components[1];
    const labels = [components[0], components[2]].map((component) => {
      const label = component.replace(/\[[^\]]+]/, (str) =>
        str.slice(1, -1).replace(formatRegex, (match) => formats[match](date, locale)),
      );

      return {
        label,
        type: component.indexOf('yy') !== -1 ? 'year' : 'month',
      };
    });

    return {
      labels,
      separator,
    };
  };
})();

// Parsers and Formaters
export const DEFAULT_FORMATS = {
  date: 'yyyy-MM-dd',
  daterange: 'yyyy-MM-dd',
  datetime: 'yyyy-MM-dd HH:mm:ss',
  datetimerange: 'yyyy-MM-dd HH:mm:ss',
  month: 'yyyy-MM',
  time: 'HH:mm:ss',
  timerange: 'HH:mm:ss',
  year: 'yyyy',
};

export const RANGE_SEPARATOR = ' - ';

const DATE_FORMATTER = function _DATE_FORMATTER(value, format) {
  return formatDate(value, format);
};

const DATE_PARSER = function _DATE_PARSER(text, format) {
  return parseDate(text, format);
};

const RANGE_FORMATTER = function _RANGE_FORMATTER(value, format) {
  if (Array.isArray(value) && value.length === 2) {
    const start = value[0];
    const end = value[1];

    if (start && end) {
      return formatDate(start, format) + RANGE_SEPARATOR + formatDate(end, format);
    }
  } else if (!Array.isArray(value) && value instanceof Date) {
    return formatDate(value, format);
  }

  return '';
};

const RANGE_PARSER = function _RANGE_PARSER(text, format) {
  const array = Array.isArray(text) ? text : text.split(RANGE_SEPARATOR);

  if (array.length === 2) {
    const range1 = array[0];
    const range2 = array[1];

    return [
      range1 instanceof Date ? range1 : parseDate(range1, format),
      range2 instanceof Date ? range2 : parseDate(range2, format),
    ];
  }

  return [];
};

export const TYPE_VALUE_RESOLVER_MAP = {
  date: {
    formatter: DATE_FORMATTER,
    parser: DATE_PARSER,
  },
  daterange: {
    formatter: RANGE_FORMATTER,
    parser: RANGE_PARSER,
  },
  datetime: {
    formatter: DATE_FORMATTER,
    parser: DATE_PARSER,
  },
  datetimerange: {
    formatter: RANGE_FORMATTER,
    parser: RANGE_PARSER,
  },
  default: {
    formatter(value) {
      if (!value) {
        return '';
      }

      return `${value}`;
    },
    parser(text) {
      if (text === undefined || text === '') {
        return null;
      }

      return text;
    },
  },
  month: {
    formatter: DATE_FORMATTER,
    parser: DATE_PARSER,
  },
  multiple: {
    formatter: (value, format) =>
      value
        .filter(Boolean)
        .map((date) => formatDate(date, format))
        .join(','),
    parser: (value, format) => {
      const values = typeof value === 'string' ? value.split(',') : value;

      return values.map((val) => {
        let valueItem = val;

        if (valueItem instanceof Date) {
          return valueItem;
        }

        if (typeof valueItem === 'string') {
          valueItem = valueItem.trim();
        } else if (typeof valueItem !== 'number' && !valueItem) {
          valueItem = '';
        }

        return parseDate(valueItem, format);
      });
    },
  },
  number: {
    formatter(value) {
      if (!value) {
        return '';
      }

      return `${value}`;
    },
    parser(text) {
      const result = Number(text);

      if (!Number.isNaN(text)) {
        return result;
      }

      return null;
    },
  },
  time: {
    formatter: DATE_FORMATTER,
    parser: DATE_PARSER,
  },
  timerange: {
    formatter: RANGE_FORMATTER,
    parser: RANGE_PARSER,
  },
  year: {
    formatter: DATE_FORMATTER,
    parser: DATE_PARSER,
  },
};
