<template>
  <ul :class="classes">
    <slot></slot>
  </ul>
</template>

<script>
const prefixCls = 'ivu-timeline';

export default {
  name: 'Timeline',

  props: {
    pending: {
      default: false,
      type: Boolean,
    },
  },

  computed: {
    classes() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-pending`]: this.pending,
        },
      ];
    },
  },
};
</script>
